import React, { Component } from 'react';
// import logo from './logo.svg';
// import './App.css';
import Polygon7 from '../img/Polygon 7.svg';

import Polygon6 from '../img/Polygon 6.png';
import Polygon3 from '../img/Polygon 3.png';
import Polygon2 from '../img/Polygon 2.png';
import Polygon1 from '../img/Polygon 1.png';
import Polygon5 from '../img/Polygon 5.png';

import Polygon9 from '../img/Polygon 9.png';
import Polygon8 from '../img/Polygon 8.png';

import Polygon4 from '../img/Polygon 4.png';

// мелкие блоки без букв.
import Polygon14 from '../img/Polygon 14.png';
import Polygon13 from '../img/Polygon 13.png';
import Polygon11 from '../img/Polygon 11.png';
import Polygon12 from '../img/Polygon 12.png';

import Polygon10 from '../img/Polygon 10.png';

class App extends Component {
  render() {
    return (
      <React.Fragment>
      <h1 style={{fontSize:"75px",position:"absolute"}}>Как мы<br/> работаем?</h1>
        <div className="lineOne">
        <img src={Polygon13}  style={{width:"316.68",position:"relative",top:"+155px",right:"70px"}}/>
          <img src={Polygon7}  style={{width:"316.68",position:"relative",top:"+55px",left:"190px"}}/>
          <img src={Polygon14} style={{width:"221.96",position:"relative",bottom:"40px",left:"150px"}}/>

  <img src={Polygon11}  style={{width:"316.68",position:"relative",top:"+125px",left:"190px"}}/>
  <img src={Polygon12}  style={{width:"316.68",position:"relative",top:"+75px",left:"180px"}}/>
        </div>
        <div className="lineTwo">
        <img src={Polygon6}  style={{width:"321.27",position:"relative",top:"+70px",left:"+140px"}}/>
          <img src={Polygon3}  style={{width:"321.27",position:"relative",top:"-70px",left:"+70px"}}/>
          <img src={Polygon2}  style={{width:"321.27",position:"relative",top:"+70px"}}/>
          <img src={Polygon1}  style={{width:"321.27",position:"relative",top:"-70px",right:"+70px"}}/>
          <img src={Polygon5}  style={{width:"321.27",position:"relative",top:"+70px",right:"+140px"}}/>
        </div>
        <div className="lineThre">
        <img src={Polygon10}  style={{width:"169.42",position:"relative",top:"10px",right:"140px"}}/>
        <img src={Polygon9}  style={{width:"321.27",position:"relative",bottom:"70px",right:"170px"}}/>
        <img src={Polygon8}  style={{width:"321.27",position:"relative",bottom:"70px",left:"5px"}}/>

        </div>
        <div className="lineFour">
        <img src={Polygon4}  style={{width:"321.27",position:"relative",bottom:"200px"}}/>

        </div>
      </React.Fragment>
    );
  }
}

export default App;



